  position: fixed;
  display: inline-block;
  top: 90px;
  right: 0;
  left: auto;
  z-index: 1003;
  color: var(--sg-green);
  font-size: 15px; }
  .cart .cart-icon {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    background-color: var(--sg-cream);
    padding: 10px;
    white-space: nowrap;
    z-index: 1007; }
    .cart .cart-icon h2 {
      font-size: 15px;
      font-weight: 100;
      text-align: right;
      margin: 0; }
      .cart .cart-icon h2 i {
        font-size: 15px; }
    .cart .cart-icon button, .cart .cart-icon p, .cart .cart-icon i {
      font-size: 15px; }
  .cart button {
    font-size: 20px; }
  @media (min-width: 769px) {
    .cart {
      top: 90px;
      right: 0;
      left: auto;
      font-size: 25px; }
      .cart .cart-icon h2, .cart button {
        font-size: 25px; }
      .cart .cart-icon {
        padding: 20px; }
        .cart .cart-icon h2 {
          text-align: left; }
          .cart .cart-icon h2 i {
            font-size: 25px; } }

.arrow-up {
  transform: rotate(0deg);
  margin-left: 10px;
  transition: 0.2s; }

.arrow-down {
  transform: rotate(-90deg);
  margin-left: 10px;
  transition: 0.2s; }

.button {
  background-color: var(--sg-green);
  color: var(--sg-cream);
  border: none;
  font-size: 15px;
  padding: 10px 30px;
  margin: 5px;
  transition: 0.2s; }
  .button:hover {
    cursor: pointer;
    background-color: #334233;
    transition: 0.2s; }
  @media (min-width: 769px) {
    .button {
      font-size: 25px; } }

.items {
  background-color: var(--sg-cream); }
  .items .cart-title {
    margin: 0;
    padding: 12px;
    font-size: 15px; }
    .items .cart-title__border {
      height: 1px;
      width: 100%;
      background-color: var(--sg-green); }
  .items .cart-item {
    text-align: center;
    font-size: 15px;
    padding: 2px 20px; }
    .items .cart-item p {
      margin-top: 0px; }
  @media (min-width: 769px) {
    .items .cart-title {
      font-size: 25px;
      padding: 22px; }
    .items .cart-item {
      font-size: 20px; } }

