<script>
import cart from "shared/stores/cart.store.js";
export let name, description, amount, bullets, images, baseAmount;
let isInCart = false;
$: buttonText = isInCart ? 'Remove from cart' : 'Add to cart'

const decimalAdder = () => {
    if(amount < 1000) {
    return "$" + (amount/100) + "0";
    } else {
        return "$" + (amount/100);
    }
}
const toggleAdd = () => {
    let cartItem = {
            name, 
            amount, 
            description, 
            currency: 'usd', 
            quantity: 1, 
            images: images.main
        }
    isInCart = !isInCart;
    if(isInCart) {
        cart.update(cart => ([...cart, cartItem]));
    } else {
        cart.update(cart => cart.filter(item => item.name !== cartItem.name));
    }
} 
</script>

